:global(.blind) {
  position: absolute;
  clip: rect(0, 0, 0, 0);
  white-space: nowrap;
  border: 0; }

:global(.clear) {
  clear: both; }
  :global(.clear)::after {
    content: ' ';
    clear: both;
    display: table; }

:global(.transparent) {
  -webkit-box-shadow: none;
          box-shadow: none;
  background-color: transparent;
  border: 0 none;
  cursor: pointer; }

:global(.ellipsis) {
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
  word-break: normal; }

html,
body {
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Helvetica Neue, Helvetica, PingFang SC, Hiragino Sans GB, Microsoft YaHei, SimSun, sans-serif;
  font-size: 16px;
  height: 100%; }
